import React from "react";
import HighlightOffIcon from "@material-ui/icons/HighlightOff";

const Note = (props) => {
	const deleteClick = () => props.onDelete(props.id);

	return (
		<div className="note">
			<h1>{props.title}</h1>
			<p>{props.content}</p>
			<button onClick={deleteClick}>
				<HighlightOffIcon />
			</button>
		</div>
	);
};

export default Note;
